import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { CuentaProvider } from '../../providers/cuenta/cuenta';
import { CurrencyPipe } from '@angular/common';

@Component({
  selector: 'page-cuentas',
  templateUrl: 'cuentas.html',
})
export class CuentasPage {

	cuenta: any;

  constructor(
  	public navCtrl: NavController,
  	private cuentaProvider: CuentaProvider
  	) {
  	console.log("--------CuentasPage--------")
  	this.getUltimosMovimientos(68729596);
  }

  ionViewDidLoad() {

  }

  getUltimosMovimientos(numeroCuenta: number){
  	this.cuentaProvider.getAccountMovments(numeroCuenta)
  	.then(data => {
  		this.cuenta = data;
  	}, err => {
  		console.log(err);
  	});
  }



}



