import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Environment } from '../../environment';

/*
  Generated class for the CuentaProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class CuentaProvider {

  constructor(public http: HttpClient) {
   console.log("--------CuentaProvider--------")
  }

  getAccountMovments(accountNumber: number) {
    return new Promise((resolve, reject) => {
      this.http.get(Environment.remote_serve_url + Environment.ultimos_movimientos + accountNumber)
      .subscribe(res => {
        resolve(res['data'][0]);
      }, (err) => {
        reject(err);
      });
    });
  }
}



