import { BrowserModule } from '@angular/platform-browser';
import { NgModule, ErrorHandler } from '@angular/core';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';
import { HttpClientModule } from '@angular/common/http';

import { ConsumoPage } from '../pages/consumo/consumo';
import { CuentasPage } from '../pages/cuentas/cuentas';
import { FeedbackPage } from '../pages/feedback/feedback';
import { TarjetasPage } from '../pages/tarjetas/tarjetas';
import { TransferenciasPage } from '../pages/transferencias/transferencias';

import { TabsPage } from '../pages/tabs/tabs';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { CuentaProvider } from '../providers/cuenta/cuenta';

@NgModule({
  declarations: [
    MyApp,
    ConsumoPage,
    CuentasPage,
    FeedbackPage,
    TarjetasPage,
    TransferenciasPage,
    TabsPage
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    ConsumoPage,
    CuentasPage,
    FeedbackPage,
    TarjetasPage,
    TransferenciasPage,
    TabsPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    CuentaProvider
  ]
})
export class AppModule {}



